import React, { useState, useEffect } from "react"
import styled from "@emotion/styled"
import { useSpring, animated } from "react-spring"
import marked from "marked"
import sanitizeHtml from "sanitize-html"
import dimensions from "styles/dimensions"
import withFocusTrap from "../../../lib/withFocusTrap"
import ProjectCard, { ActionButtons } from "../../ProjectCard"

const ViewContainer = styled(animated.div)`
  display: flex;
  flex-direction: column;
  background: white;
  padding: 10px 20px;
  z-index: 1001;
  width: 70%;
  max-height: 90%;
  box-shadow: 0px 9px 24px rgba(0, 0, 0, 0.06);

  border-radius: 5px;
  overflow-y: hidden;

  div {
    margin-bottom: 5px;
  }

  @media (max-width: ${dimensions.maxwidthTablet}px) {
    width: 85%;
    min-height: 80%;
  }

  @media (max-width: ${dimensions.maxwidthMobile}px) {
    width: 99%;
    min-height: 99%;

    .project-card-preview {
      display: none;
    }
  }
`

// configure marked to parse github flavoured markdown
marked.setOptions({
  renderer: new marked.Renderer(),
  gfm: true,
})

const ReadMeContainer = styled("div")`
  overflow-y: scroll;

  h1 {
    font-size: 1.6em;
  }

  h2 {
    font-size: 1.3em;
  }

  h3 {
    font-size: 1.1em;
  }

  h4 {
    font-size: 0.8em;
  }

  h1,
  h2,
  h3,
  h4,
  p {
    margin: 8px 0 15px 0;
  }

  p {
    line-height: 1.5;
  }
`

const ActionButtonContainer = styled("div")`
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-end;
`

const ReadMe = props => {
  const { repoName } = props
  const [data, setData] = useState({
    loading: true,
    error: false,
    payload: "",
  })

  useEffect(() => {
    fetch(
      `https://raw.githubusercontent.com/imccausl/${repoName}/master/README.md`
    ).then(response =>
      response.text().then(data => {
        setData({ loading: false, payload: data, error: false })
      })
    )
  }, [repoName])

  const renderMarkdown = data => {
    const cleaned = sanitizeHtml(data)

    return { __html: marked(cleaned) }
  }

  const { loading, error, payload } = data

  return (
    !loading &&
    !error && (
      <ReadMeContainer
        dangerouslySetInnerHTML={renderMarkdown(payload)}
      ></ReadMeContainer>
    )
  )
}

const ViewReadme = props => {
  const {
    visible,
    title,
    repoName,
    description,
    thumbnail,
    updated_at,
    repo,
    website,
  } = props

  const { x } = useSpring({
    from: { x: 0 },
    x: visible ? 1 : 0,
    config: { duration: 400 },
  })

  return (
    <ViewContainer
      style={{
        transform: x
          .interpolate({
            range: [0, 0.25, 0.35, 0.45, 0.55, 0.65, 0.75, 1],
            output: [0, 0.5, 0.9, 1],
          })
          .interpolate(x => `scale(${x})`),
      }}
    >
      <ProjectCard
        title={title}
        description={description}
        thumbnail={thumbnail}
        updated_at={updated_at}
        repo={repo}
        website={website}
        hideReadMoreButton={true}
        hideActionButtons={true}
      />
      <ReadMe repoName={repoName} />
      <ActionButtons repo={repo} website={website} />
    </ViewContainer>
  )
}

export default withFocusTrap(ViewReadme)
